.myModal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0, 0, 0, 0.5);
}

.myModalContent {
    padding: 25px;
    background: white;
    border-radius: 16px;
    min-width: 400px;
    max-width: 600px;
}

.myModal.active {
    display: flex;
    justify-content: center;
    align-items: center;
}